.searchForm {
    display: flex;
    margin-bottom: 20px;
    justify-content: center;
    padding-top: 20px;
  }
  
  .searchInput {
    padding: 10px;
    border: 1px solid #ffffff;
    border-radius: 4px;
    flex: 1;
    font-size: 16px;
    max-width: 300px;
  }
  
  .searchButton {
    background-color: rgb(0, 255, 255);
    color: rgb(0, 43, 236);
    border: none;
    border-radius: 4px;
    padding: 10px 20px;
    margin-left: 10px;
    cursor: pointer;
    font-size: 16px;
  }
  
  .searchButton:hover {
    background-color: rgb(253, 122, 0);
  }